<template>
  <v-col cols="11" md="4" sm="6">
    <v-card class="py-8 px-5 white black--text">
      <h1 class="text-center">{{ $t('auth.register.heading') }}</h1>
      <v-card-text>
        <base-alert display="register-page" />
        <v-form ref="form" v-model="valid" @submit.prevent="register">
          <div>
            <v-text-field
              v-model="user.name"
              color="primary"
              prepend-icon="mdi-account"
              :label="$t('auth.register.form.labels.name')"
              :rules="[rules.required]"
              light
            />
          </div>
          <div>
            <v-text-field
              v-model="user.email"
              color="primary"
              prepend-icon="mdi-email"
              type="email"
              :label="$t('auth.register.form.labels.email')"
              :rules="[rules.required, rules.isEmail]"
              light
            />
          </div>
          <div>
            <v-text-field
              v-model="user.password"
              color="primary"
              prepend-icon="mdi-lock"
              :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
              :type="showPassword ? 'text' : 'password'"
              :label="$t('auth.register.form.labels.password')"
              :rules="[rules.required, rules.passwordMinLength]"
              light
              @click:append="showPassword = !showPassword"
            />
          </div>
          <div>
            <v-text-field
              v-model="confirmPassword"
              color="primary"
              prepend-icon="mdi-lock"
              type="password"
              :label="$t('auth.register.form.labels.confirmPassword')"
              :rules="[
                rules.required,
                rules.confirmPassword(confirmPassword, user.password)
              ]"
              light
            />
          </div>
          <div>
            <v-btn
              :loading="$store.state.loading.auth.register"
              color="primary"
              block
              type="submit"
              >{{ $t('auth.register.button.text') }}</v-btn
            >
          </div>
          <v-divider></v-divider>
        </v-form>
        <div class="text-center mt-4 black--text">
          {{ $t('auth.register.footerText') }}
          <nuxt-link to="/auth/login">{{
            $t('auth.register.links.goToLogin')
          }}</nuxt-link>
        </div>
      </v-card-text>
    </v-card>
  </v-col>
</template>

<script>
import { mapGetters } from 'vuex'
import validations from '@/mixins/validations'
export default {
  mixins: [validations],
  data() {
    return {
      user: {
        name: '',
        email: '',
        role: '',
        password: ''
      },
      confirmPassword: '',
      showPassword: false
    }
  },
  computed: {
    ...mapGetters('role', ['getRole'])
  },
  methods: {
    async register() {
      try {
        this.$refs.form.validate()
        if (this.valid) {
          this.$store.commit('alert/REMOVE_ALERT')
          this.$store.commit('loading/SET_LOADING', {
            option: 'auth.register',
            value: true
          })
          this.user.role = this.getRole('user') ? this.getRole('user')._id : ''
          await this.$axios.post('/api/auth/register', this.user)

          this.$store.commit('loading/SET_LOADING', {
            option: 'auth.register',
            value: false
          })
          const alert = {
            type: 'success',
            display: 'login-page',
            show: true,
            text: 'successMessages.auth.userRegisterd'
          }
          this.$store.commit('alert/SET_ALERT', alert)
          this.$router.push('/auth/login')
        }
      } catch (e) {
        const alert = {
          type: 'error',
          display: 'register-page',
          show: true,
          text: e.response ? e.response.data.message : e.message
        }
        this.$store.commit('alert/SET_ALERT', alert)
        this.$store.commit('loading/SET_LOADING', {
          option: 'auth.register',
          value: false
        })
      }
    }
  }
}
</script>

